$font-stack: Trebuchet MS, Droid Sans;

html, button, input, select, textarea, .pure-g [class *= "pure-u"] {
    font-family: $font-stack;
}

body {
    color: #000;
    font-size: 18px;
    max-width: 700px;
    margin: 0 auto;
    padding: 16px;
}

h1, h2, h4, h5, h6, .title {
    font-family: $font-stack;
    font-weight: normal;
}

a, .linkcolor, .title {
    color: #ff0000;
    text-decoration: none;
}

.nav a {
    text-decoration: none;
    display: inline-block;
    margin: 0 1em 0 0;
}

.nav {
    padding: 1em 0px 0.5em;
    margin-bottom: 2em;
    border-bottom: 1px solid #eee;
}

.footer {
    text-align: center;
    padding-top: 55px;
    padding-bottom: 44px;
}

.footer a {
    color: #aaa;
    text-decoration: underline
}

.left {
    float: left !important;
}
.right {
    float: right !important;
}
.clear {
    clear: both !important;
}
.margintop1 {
    margin-top: 1em !important;
}
.margintop2 {
    margin-top: 2em !important;
}
.inlineblock {
    display: inline-block !important;
}
.centertext {
    text-align: center
}

.borderedbutton {
    font-family: $font-stack;
    color: #ff0000;
    border-color: #ff0000;
    display: inline-block;
    border: 1px solid;
    border-radius: 8px;
    padding: 7px 16px 8px 16px;
    background-color: transparent;
    text-decoration: none;
    -webkit-tap-highlight-color: transparent;
    -webkit-user-select: none;
    -webkit-touch-callout: none;
}

.borderedbutton:active {
    background-color: #ff0000;
    color: #ffffff
}

.borderedbutton:focus {
    outline: 0
}

.navlink {
   font-family: $font-stack;
   text-transform: capitalize;
}

.search_container {
    position: relative;
}

#productsearch {
    width: 180px;
    font-size: 0.74em;
    padding: 0.25em 2em;
}

.alert {
    text-align: center !important;
}

.alert {
    color: #fc1b0f;
    border-color: #fc1b0f;
}